/**
 * CLEAR VISUAL KEYBOARD FOCUS
 *
 * This relies on what-input.js:
 *
 * suppress focus ring on form controls
 * for mouse and touch users.
 */
[data-whatintent=mouse] *:focus,
[data-whatintent=touch] {
  outline: none;
}

[data-whatintent=keyboard] {
  /**
  * force clear visual focus for
  * keyboard navigation.
  */
  /**
  * special overrides for checkboxes
  * and radio buttons.
  */
}
[data-whatintent=keyboard] *:focus {
  outline: 2px dashed #8e0085;
  outline-offset: 2px;
}
[data-whatintent=keyboard] input[type=radio]:focus input[type=checkbox]:focus + label:before {
  outline: 2px dashed #8e0085;
  outline-offset: 2px;
}

/* accessible skip-navigation link */
.skip-link {
  font-family: "Kumbh Sans", sans-serif;
  padding: 4px;
  font-size: 20px;
  font-weight: 600;
  text-transform: uppercase;
}

.visually-hidden.focusable.skip-link:focus, .visually-hidden.focusable.accessibility:focus, .visually-hidden.focusable.skip-filters:focus {
  position: fixed !important;
  top: 0;
  left: 0;
  z-index: 900;
  clip: auto;
  height: auto;
  width: auto;
  font-size: 14px;
  color: white;
  background-color: black;
  padding: 0.75em;
  outline: none;
  border: 2px dashed #8e0085 !important;
}

[data-whatintent=keyboard] .layer--dark-background *:focus {
  outline: 2px dashed white;
}
[data-whatintent=keyboard] .layer--dark-background .form-radio:focus + label:before,
[data-whatintent=keyboard] .layer--dark-background .form-checkbox:focus + label:before,
[data-whatintent=keyboard] .layer--dark-background .facets-checkbox:focus + label:before,
[data-whatintent=keyboard] .layer--dark-background .facets-radio:focus + label:before {
  outline: 2px dashed white;
}

.layer--dark-background .visually-hidden.focusable.skip-link:focus, .layer--dark-background .visually-hidden.focusable.accessibility:focus, .layer--dark-background .visually-hidden.focusable.skip-filters:focus {
