/* Loading Spinner */
.results-container__loader {
  display: flex;
}
.results-container__loader .spinner {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
  margin: 0 auto;
  display: none;
}
.results-container__loader .spinner.loading {
  display: block;
}
.results-container__loader .spinner div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid #fff;
  border-radius: 50%;
  animation: spinner 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #2B303B transparent transparent transparent;
}
.results-container__loader .spinner div:nth-child(1) {
  animation-delay: -0.45s;
}
.results-container__loader .spinner div:nth-child(2) {
  animation-delay: -0.3s;
}
.results-container__loader .spinner div:nth-child(3) {
  animation-delay: -0.15s;
}
@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

